import { UserService } from './../../services/userService/user.service';
import { UserByIdResolverService } from './../../services/userService/UserByIdResolverService.service';
import { UsersMessageType } from './../../services/http/resultsTypes/usersmessagetype';
import { ActivatedRoute } from '@angular/router';
import { User } from './../user-list/Struct/user';
import { APP_CONFIG } from './../../consts/app.config';
import { AppConfig } from './../../consts/app-config';
import { Component, OnInit, Inject } from '@angular/core';
import { trigger, transition, animate, style, state } from '@angular/animations';
import { Overlay } from 'ngx-modialog';
import { Modal } from 'ngx-modialog/plugins/bootstrap';

@Component({
  providers: [UserByIdResolverService, UserService],
  selector: 'app-user-page',
  templateUrl: './user-page.component.html',
  styleUrls: ['./user-page.component.css'],
  animations: [trigger('routerTransition', [
    state('void', style({width:'100%'}) ),
    state('*', style({ width:'100%'}) ),
    transition(':enter', [  // before 2.1: transition('void => *', [
      style({transform: 'translateX(100%)'}),
      animate('0.8s ease-in-out', style({transform: 'translateX(0%)'}))
    ]),
    transition(':leave', [  // before 2.1: transition('* => void', [
      style({transform: 'translateX(0%)'}),
      animate('0.8s ease-in-out', style({transform: 'translateX(-100%)'}))
    ])
  ])]
})

export class UserPageComponent implements OnInit {
  appConfig: AppConfig;
  user: User;

  constructor(public modal: Modal, @Inject(APP_CONFIG) config: AppConfig, private route: ActivatedRoute, private userService: UserService) {
    this.appConfig = config;
  }

  ngOnInit() {
    this.route.data
    .subscribe((data: { user: User }) => {
      this.user = data.user;
    });
  }
  editProfile(){
    return this.userService.updateUserById(this.user.id, this.user).subscribe(update => {
      console.log("user updated");
      const dialogRef = this.modal.alert().size('lg').showClose(true).title('A simple Alert style modal window').body('<h1>The User has been updated!</h1>').open();
    });    
  }
}




