import { WebsocketService } from './../../services/websocketService/websocket.service';
import { Component, OnInit } from '@angular/core';
import { WebSocketConfig } from '../../services/websocketService/WebSocketConfig';

@Component({
  providers: [WebsocketService],
  selector: 'app-websocket',
  templateUrl: './websocket.component.html',
  styleUrls: ['./websocket.component.css']
})
export class WebsocketComponent implements OnInit {

  private webSocketService : WebsocketService;
  private messages : Array<string>;
  private webSocketConfig : WebSocketConfig;
  private title: string;

  constructor(websocketService: WebsocketService) 
  {
    this.webSocketService = websocketService;
    this.webSocketConfig = new WebSocketConfig("ws://localhost:60918/AircraftWebSocket/", undefined);
    this.title = "Web Socket Test";
    this.messages = new Array<string>();
  }

  ngOnInit() {
    this.webSocketService.createConnection(this.webSocketConfig);
    this.webSocketService.onOpen(()=>{
      console.log("web socket has been connected");
      //this.sendMessage("Test");
      this.onMessage();
      setInterval(() =>{
        this.sendMessage("Test");
      }, 1000);
    });
    
    
  }

  sendMessage(message : string){
    this.webSocketService.sendMessage(message);
  }

  onMessage(){
    this.webSocketService.onMessage((newMessage) =>{
      this.messages.push(newMessage.data);
    });
  }

  

}



