import { UsersMessageType } from '../../services/http/resultsTypes/usersmessagetype';
import { UserDetailsResolverService } from '../../services/userService/user-details-resolver.service';
import { UserService } from '../../services/userService/user.service';
import { User } from './Struct/user';
import { Component, OnInit, Inject, NgModule } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AppConfig } from '../../consts/app-config';
import { APP_CONFIG } from '../../consts/app.config';
import { SearchByNamePipe } from '../../pipes/searchByName.pipe';
import { trigger, transition, animate, style, state } from '@angular/animations';
import { routerTransition } from '../animations/routerAnimation';
import {DataTableModule, SharedModule} from 'primeng/primeng';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';



@NgModule({
  imports: [DataTableModule, SharedModule, BrowserAnimationsModule],
  exports: [],
  declarations: [],
  
})

@Component({
  providers: [UserService, UserDetailsResolverService],
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css'],
  animations: [trigger('routerTransition', [
    state('void', style({width:'100%'}) ),
    state('*', style({width:'100%'}) ),
    transition(':enter', [  // before 2.1: transition('void => *', [
      style({transform: 'translateX(100%)'}),
      animate('0.8s ease-in-out', style({transform: 'translateX(0%)'}))
    ]),
    transition(':leave', [  // before 2.1: transition('* => void', [
      style({transform: 'translateX(0%)'}),
      animate('0.8s ease-in-out', style({transform: 'translateX(-100%)'}))
    ])
  ])]
})
export class UserListComponent implements OnInit {
  userService: UserService;
  users: UsersMessageType[];
  appConfig: AppConfig;
  searchContent: any;
  type: Number;
  selectedUser: UsersMessageType;
  constructor(userService: UserService, private route: ActivatedRoute,
    private router: Router, @Inject(APP_CONFIG) config: AppConfig) {
    
    this.userService = userService;
    this.appConfig = config;
    this.searchContent = "";
    this.type = 1;
   }

  ngOnInit() {
      this.route.data
    .subscribe((data: { users: UsersMessageType[] }) => {
      this.users = data.users;
    });
  }

  search(){
    console.log("searching");
  }
  changeSearchBy(type){
    if(type === 0){
      this.searchContent = 1;
    }else{
      this.searchContent = "";
    }
    this.type = type;
  }
  onRowSelect(){
    console.log(this.selectedUser);
    var string = '../dashboard/users/' + this.selectedUser.id;
    this.router.navigate([string]);
  }
}



