import { User } from '../../components/user-list/Struct/user';
import { AppConfig } from '../../consts/app-config';
import { APP_CONFIG } from '../../consts/app.config';
import { IApiMessages } from '../http/resultsTypes/iapimessages';
import { UsersMessageType } from '../http/resultsTypes/usersmessagetype';
import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';


@Injectable()
export class UserService {
  appConfig: AppConfig;
  http: HttpClient;

  constructor(http: HttpClient, @Inject(APP_CONFIG) config: AppConfig) { 
    this.http = http;
    this.appConfig = config;
  }

  getUsers() {
    
    return this.http.get<UsersMessageType[]>(this.appConfig.userAddress).map(response => response);
    
  }
  getUserById(id){
    return this.http.get<User>(this.appConfig.userAddress + "/" + id).map(response => response);
  }

  updateUserById(id, user){
    return this.http.put<User>(this.appConfig.userAddress + "/" + id, user).map(response => response);
  }
  
}



