import { User } from '../../components/user-list/Struct/user';
import { UsersMessageType } from '../http/resultsTypes/usersmessagetype';
import { UserService } from './user.service';
import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

@Injectable()
export class UserByIdResolverService implements Resolve<User>{
  userService: UserService;

  constructor(userService: UserService) {
      this.userService = userService;
  
   }
  
  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): User | Observable <User> | Promise <User> {
      console.log('resolver');
      let id = route.params['id'];
     return this.userService.getUserById(id).map((auth) => {
                console.log('users');
                return auth;
           
        }, x => { 
           
            return null;
        }); 
 
  }
  
}
 


