import { LoadingService } from './../loadingService/loading.service';
import { User } from '../../components/user-list/Struct/user';
import { UsersMessageType } from '../http/resultsTypes/usersmessagetype';
import { UserService } from './user.service';
import { Injectable, NgModule } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

@NgModule({
    providers:[LoadingService, UserService]
})

@Injectable()
export class UserDetailsResolverService implements Resolve<UsersMessageType[]>{

    constructor(private userService: UserService, private loadingService: LoadingService) {

    }
  
  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): UsersMessageType[] | Observable <UsersMessageType[]> | Promise <UsersMessageType[]> {
     console.log('resolver');
     this.loadingService.startLoading();
     return this.userService.getUsers().map((auth) => {
                console.log('users');
                this.loadingService.stopLoading();
                return auth;
           
        }, x => { 
            this.loadingService.stopLoading();
            return null;
    }); 
 
  }
  
}



