import { UserLoggedDetailsService } from './../../userLoggedDetailsService/userLoggedDetails.service';
import { BehaviorSubject, Observable } from 'rxjs';
import { User } from './../../../components/user-list/Struct/user';
import { IApiMessages } from '../../http/resultsTypes/iapimessages';
import { LoginFailType } from '../../http/resultsTypes/loginfailtype';
import { LoginSucceedType } from '../../http/resultsTypes/loginsucceedtype';
import { PermissionService } from '../permission.service';
import { Injectable, NgModule } from '@angular/core';
import { RouterModule, Routes, Router } from '@angular/router';

@NgModule({
  providers: [UserLoggedDetailsService],
  imports: [],
  exports: [],
  declarations: [],
  schemas: []
})

@Injectable()
export class ValidatePermissionsService {
  router: Router;
  permission: PermissionService;
  userLoggedDetailsService: UserLoggedDetailsService;
  user: LoginSucceedType;
  
  constructor(permission: PermissionService, router: Router, userLoggedDetailsService: UserLoggedDetailsService) {
    this.permission = permission;
    this.router = router;
    this.userLoggedDetailsService = userLoggedDetailsService;
    
   }
  validateLogin(message: IApiMessages, typeMessage: number){
    // failed
    if (typeMessage === 0) {
      const newMessage: LoginFailType = <LoginFailType> message;
      return newMessage; 
      
    } // succeed
    else if (typeMessage === 1) {
      this.user = <LoginSucceedType> message;
      this.userLoggedDetailsService.userUpdated(this.user);
      this.permission.setToken(this.user.token);
      this.router.navigate(['/dashboard']);
      return this.user; 
      
    }
  }
  validateUsersList(){
    

  }
}



