import { UserLoggedDetailsService } from './../userLoggedDetailsService/userLoggedDetails.service';

import { LoadingService } from './../loadingService/loading.service';
import { User } from '../../components/user-list/Struct/user';
import { UsersMessageType } from '../http/resultsTypes/usersmessagetype';
import { UserService } from './user.service';
import { Injectable, NgModule } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { LoginSucceedType } from '../http/resultsTypes/loginsucceedtype';

@NgModule({
    providers:[UserLoggedDetailsService]
})

@Injectable()
export class DashboardResolverService implements Resolve<LoginSucceedType>{

    constructor(private userLoggedDetailsService: UserLoggedDetailsService) {

    }
  
  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): LoginSucceedType | Observable <LoginSucceedType> | Promise <LoginSucceedType> {
    
    return this.userLoggedDetailsService.getUser();
  }
  
}



