import { DashboardResolverService } from './../../services/userService/dashboardresolver.service';
import { UserLoggedDetailsService } from './../../services/userLoggedDetailsService/userLoggedDetails.service';
import { LoginSucceedType } from './../../services/http/resultsTypes/loginsucceedtype';
import { User } from './../user-list/Struct/user';
import { ValidatePermissionsService } from './../../services/premission/validatePremissions/validate-permissions.service';
import { Component, OnInit, Inject, NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import {TabMenuModule, MenuItem} from 'primeng/primeng';
import { PermissionService } from '../../services/premission/permission.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AppConfig } from '../../consts/app-config';
import { APP_CONFIG } from '../../consts/app.config';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { CommonModule } from '@angular/common';
import {DropdownModule} from 'primeng/primeng';
import {SelectItem} from 'primeng/primeng';
import { Subscription } from 'rxjs';
import {ConfirmDialogModule,ConfirmationService} from 'primeng/primeng';


@NgModule({
  providers: [UserLoggedDetailsService],
  imports: [NgbModule, CommonModule, DropdownModule, ConfirmDialogModule],
  exports: [],
  declarations: [],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]
})

@Component({
  providers: [DashboardResolverService, ConfirmationService],
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  settingsFirstHidden: boolean;
  settingsSecondHidden: boolean
  settingsThirdHidden: boolean;
  dropdownValues: SelectItem[];
  appConfig: AppConfig;
  selectedCity: string;
  user: any;
  subscription: Subscription;
  mouseOnMenus: boolean[];

  constructor(private confirmationService: ConfirmationService, private route: ActivatedRoute, private userLoggedDetailsService : UserLoggedDetailsService,private permissionService: PermissionService, private router: Router, @Inject(APP_CONFIG) config: AppConfig) {
    this.appConfig = config;
    this.dropdownValues = [];
    this.dropdownValues.push({label:'Select City', value:null});
    this.dropdownValues.push({label:'New York', value:{id:1, name: 'New York', code: 'NY'}});
    this.dropdownValues.push({label:'Rome', value:{id:2, name: 'Rome', code: 'RM'}});
    this.dropdownValues.push({label:'London', value:{id:3, name: 'London', code: 'LDN'}});
    this.dropdownValues.push({label:'Istanbul', value:{id:4, name: 'Istanbul', code: 'IST'}});
    this.dropdownValues.push({label:'Paris', value:{id:5, name: 'Paris', code: 'PRS'}});

    this.settingsFirstHidden = true;
    this.settingsSecondHidden = true;
    this.settingsThirdHidden = true;
    this.user = {
      firstName: "elad",
      lastName: "israeli"
    }

    //init the mounseOnMenus to false - is it indicate if the specific menu is open.
    this.mouseOnMenus = [];
    this.resetAllMenusClicked();
  }
  ngOnInit() {
    this.route.data
    .subscribe(data => {
      this.user = data.user;
    });
  }
  changePage(index){
    this.resetAllMenusClicked();
    switch(index){
      case 1:
        if(this.permissionService.isAuthenticated()){
          this.mouseOnMenus[index] = true;  
          this.router.navigate(['dashboard/users']);
        }else{
          
        }
      break;
      case 2:
      if(this.permissionService.isAuthenticated()){
        this.mouseOnMenus[index] = true;
        this.router.navigate(['dashboard/statistics']);
      }else{
        
      }
      break;
      case 3:
      if(this.permissionService.isAuthenticated()){
        this.mouseOnMenus[index] = true;
        this.router.navigate(['dashboard/users']);
      }else{
        
      }
      break;
      case 4:
      if(this.permissionService.isAuthenticated()){
        this.mouseOnMenus[index] = true;
        this.router.navigate(['dashboard/users']);
      }else{
        
      }
      break;
      case 5:
      if(this.permissionService.isAuthenticated()){
        this.mouseOnMenus[index] = true;
        this.router.navigate(['dashboard/users']);
      }else{
        
      }
      break;
      case 6:
      if(this.permissionService.isAuthenticated()){
        this.mouseOnMenus[index] = true;
        this.router.navigate(['dashboard/schedule']);
      }else{
        
      }
      break; 
    }
  
  }
  resetAllMenusClicked(){
    for(var i = 0 ; i < this.mouseOnMenus.length ; i++){
      this.mouseOnMenus[i] = false;
    }
  }
  openBlock(index){
   switch(index){
     case 1:
     this.settingsFirstHidden=!this.settingsFirstHidden;
     this.settingsSecondHidden = true;
     this.settingsThirdHidden = true;
     break;
     case 2:
     this.settingsSecondHidden=!this.settingsSecondHidden;
     this.settingsFirstHidden = true;
     this.settingsThirdHidden = true;
     break;
     case 3:
     this.settingsThirdHidden=!this.settingsThirdHidden;
     this.settingsFirstHidden = true;
     this.settingsSecondHidden = true;
     break;
     case 4:
     break;
     case 5:
     break;
   } 
  }
  logout(){
    console.log("attempt to logout");
    this.confirmationService.confirm({
      message: 'Are you sure that you want to proceed?',
      header: 'Confirmation',
      icon: 'fa fa-question-circle',
      accept: () => {
        this.permissionService.deleteToken();
        this.router.navigate(['login']);   
      },
      reject: () => {
        console.log("rejected logout");
      }
  });

  
  }

}



