import { Component, OnInit, NgModule, ViewChild } from '@angular/core';
import { ChartModule, SelectItem } from 'primeng/primeng';

@NgModule({
  providers: [],
  imports: [ChartModule],
  exports: [],
  declarations: [],
  schemas: []
})

@Component({
  selector: 'app-StatisticsPage',
  templateUrl: './StatisticsPage.component.html',
  styleUrls: ['./StatisticsPage.component.css']
})
export class StatisticsPageComponent implements OnInit {
  data: any;
  selectedOptionsEntrance: any;
  options: SelectItem[];
  
  constructor() { 

    this.data = {
      labels: ['', '', '', '', '', '', '', '', '', '', '', ''],
      datasets: [
          {
              label: 'First Dataset',
              data: [65, 59, 80, 81, 56, 55, 40],
              fill: false,
              borderColor: '#4bc0c0'
          },
          {
              label: 'Second Dataset',
              data: [28, 48, 40, 19, 86, 27, 90],
              fill: false,
              borderColor: '#565656'
          }
      ]
    }

    this.options = [];
    this.options.push({label:'', value:{id:1, name: 'New York', code: 'NY'}});      
    this.options.push({label:'', value:{id:2, name: 'New York', code: 'NY'}});      
    this.options.push({label:'', value:{id:3, name: 'New York', code: 'NY'}});      
    this.options.push({label:'', value:{id:4, name: 'New York', code: 'NY'}});      

  }

  ngOnInit() {
  }
  onPickEntranceOption(){
    console.log("onPickEntranceOption");
    switch(this.selectedOptionsEntrance.id){
      case 1:
      this.data = {
        labels: [' ', ' ', ' ', ' ', ' ', ' ', ' '],
        datasets: [
            {
                label: 'First Dataset',
                data: [65, 59, 80, 81, 56, 55, 40],
                fill: false,
                borderColor: '#4bc0c0'
            },
            {
                label: 'Second Dataset',
                data: [28, 48, 40, 19, 86, 27, 90],
                fill: false,
                borderColor: '#565656'
            }
        ]
      }
      break;
      case 2:
      this.data = {
        labels: [' ', ' ', ' ', ' ', ' ', ' ', ' '],
        datasets: [
            {
                label: 'First Dataset',
                data: [65, 59, 80, 81, 56, 55, 40],
                fill: false,
                borderColor: '#4bc0c0'
            },
            {
                label: 'Second Dataset',
                data: [28, 48, 40, 19, 86, 27, 90],
                fill: false,
                borderColor: '#565656'
            }
        ]
      }
      break;
      case 3:
      this.data = {
        labels: ['', '', '', '', '', '', '', '', '', '', '', ''],
        datasets: [
            {
                label: 'First Dataset',
                data: [65, 59, 80, 81, 56, 55, 40],
                fill: false,
                borderColor: '#4bc0c0'
            },
            {
                label: 'Second Dataset',
                data: [28, 48, 40, 19, 86, 27, 90],
                fill: false,
                borderColor: '#565656'
            }
        ]
      }
      break;
      case 4:
      this.data = {
        labels: ['2017', '2016', '2015', '2014', '2013', '2012', '2011', '2010', '2009', '2008', '2007'],
        datasets: [
            {
                label: 'First Dataset',
                data: [65, 59, 80, 81, 56, 55, 40],
                fill: false,
                borderColor: '#4bc0c0'
            },
            {
                label: 'Second Dataset',
                data: [28, 48, 40, 19, 86, 27, 90],
                fill: false,
                borderColor: '#565656'
            }
        ]
      }
      break;
    }
  }
}



