import { Component, OnInit, CUSTOM_ELEMENTS_SCHEMA, NgModule, Input } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TooltipComponent } from '../tooltip/tooltip.component';
import { BrowserModule } from '@angular/platform-browser';

@NgModule({
  providers: [],
  imports: [NgbModule, BrowserModule],
  exports: [],
  declarations: [],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]
})

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss'],
})
export class MapComponent implements OnInit {
  public showTooltip: Boolean;
  public styles: Object;
  public dynamicButtonStyles: Object;

  constructor() { 
    this.showTooltip = false;
  }

  ngOnInit() {
    this.styles = {
      'top' : '0px',
      'left' : '0px',
      'position': 'fixed',
    };
  }

  onClickMap(position){
    console.log("Clicked On Map");
    console.log(position);
    this.styles["top"] = `${position.y}px`
    this.styles["left"] = `${position.x}px`
    this.showTooltip = true;
  }
  onRightClick(){
    this.showTooltip = false;
  }

}



