import { LoadingService } from './../../services/loadingService/loading.service';
import { Router } from '@angular/router';
import { AppConfig } from '../../consts/app-config';
import { APP_CONFIG } from '../../consts/app.config';
import { IApiMessages } from '../../services/http/resultsTypes/iapimessages';
import { ValidatePermissionsService } from '../../services/premission/validatePremissions/validate-permissions.service';
import { Component, OnInit, Inject, CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import {MessagesModule} from 'primeng/primeng';
import { Modal } from 'ngx-modialog/plugins/bootstrap';

@NgModule({
  providers: [ValidatePermissionsService, LoadingService],
  imports: [MessagesModule],
})

@Component({
  providers: [ValidatePermissionsService],
  selector: 'app-loginform',
  templateUrl: './loginform.component.html',
  styleUrls: ['./loginform.component.css']
})
export class LoginformComponent implements OnInit {
  validatePermission: ValidatePermissionsService;
  appConfig: AppConfig;
  username: string;
  password: string;
  answer: string;
  http: HttpClient;
  constructor(private modal: Modal, http: HttpClient, @Inject(APP_CONFIG) config: AppConfig, validatePermission: ValidatePermissionsService, private router: Router, private loadingService: LoadingService) {
    this.answer = 'no answer';
    this.http = http;
    this.appConfig = config;
    this.validatePermission = validatePermission;
  }
  ngOnInit() {
  }
  onSubmit() {
    this.loadingService.startLoading();
    var body = {
      username: this.username,
      password: this.password
    }

    let headers = new HttpHeaders();
    headers.set('Content-Type', 'application/json');
    let req = this.http.post<IApiMessages>(this.appConfig.loginAddress, body, {headers: headers});
    req.subscribe(data => {
      this.loadingService.stopLoading();
      var obj = this.validatePermission.validateLogin(data, 1);
    }, err => {
      this.loadingService.stopLoading();
      const dialogRef = this.modal.alert().size('lg').showClose(true).title('<div style="text-align: center;"> A simple Alert style modal window </div>').body('<h1>The User has been updated!</h1>').open();
    });
   
  }

}



