import { LoginSucceedType } from './../http/resultsTypes/loginsucceedtype';
import { UserLoggedDetailsService } from './../userLoggedDetailsService/userLoggedDetails.service';
import { PermissionService } from './permission.service';
import { Injectable, NgModule } from '@angular/core';
import { CanActivateChild, ActivatedRouteSnapshot, RouterStateSnapshot, CanActivate, Router } from '@angular/router';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/Rx';

@NgModule({
  providers: [UserLoggedDetailsService],
  imports: [],
  exports: [],
  declarations: [],
  schemas: []
})

@Injectable()
export class CanActivateTeamService implements CanActivate {
  router: Router;
  userLoggedDetailsService: UserLoggedDetailsService;
  permission: PermissionService;


  constructor(permission: PermissionService, router: Router, userLoggedDetailsService: UserLoggedDetailsService) {
    this.permission = permission;
    this.router = router;
    this.userLoggedDetailsService = userLoggedDetailsService;
   }
  
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | Observable<boolean> | Promise<boolean> {
       return this.permission.isAuthenticated().map(e => {
        if (e) {
            console.log(e);
            let eParse = <LoginSucceedType> e;
            this.userLoggedDetailsService.userUpdated(eParse);
            return true;
        }
    }).catch(() => {
        this.router.navigate(['/login']);
        return Observable.of(false);
    });
  }

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | Observable<boolean> | Promise<boolean> {
    return this.canActivate(route, state);
  }
 
}








