import { WebsocketComponent } from './components/websocket/websocket.component';
import { DashboardResolverService } from './services/userService/dashboardresolver.service';
import { UserLoggedDetailsService } from './services/userLoggedDetailsService/userLoggedDetails.service';
import { LoadingService } from './services/loadingService/loading.service';
import { UserByIdResolverService } from './services/userService/UserByIdResolverService.service';
import { SearchByNamePipe } from './pipes/searchByName.pipe';
import {BrowserModule} from '@angular/platform-browser';
import {NgModule, CUSTOM_ELEMENTS_SCHEMA} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { ChartModule, MessagesModule } from 'primeng/primeng';

import '../../node_modules/primeng/resources/themes/omega/theme.css';
import '../../node_modules/primeng/resources/primeng.min.css';

import {AppComponent} from './app.component';
import {LoginformComponent} from './components/loginform/loginform.component';
import {LogoComponent} from './components/logo/logo.component';
import {APP_DI_CONFIG} from './consts/app-config';
import {APP_CONFIG} from './consts/app.config';
import {PageNotFoundComponent} from './components/page-not-found/page-not-found.component';
import {CanActivateTeamService} from './services/premission/can-activate-team.service';
import {HttpClientModule} from '@angular/common/http';
import {DashboardComponent} from './components/dashboard/dashboard.component';
import {UserListComponent} from './components/user-list/user-list.component';
import {AuthInterceptorService} from './services/http/interceptors/auth-interceptor.service';
import {JwtHelperService} from './services/premission/jwt/jwt-helper.service';
import { UserDetailsResolverService } from './services/userService/user-details-resolver.service';
import {HTTP_INTERCEPTORS} from '@angular/common/http';
import { TabMenuModule, DropdownModule } from 'primeng/primeng';
import { PermissionService } from './services/premission/permission.service';
import { UserService } from './services/userService/user.service';
import { UserPageComponent } from './components/user-page/user-page.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ModalModule } from 'ngx-modialog';
import { BootstrapModalModule } from 'ngx-modialog/plugins/bootstrap';
import { StatisticsPageComponent } from './components/StatisticsPage/StatisticsPage.component';
import {DataTableModule,SharedModule} from 'primeng/primeng';
import {OrderListModule} from 'primeng/primeng';
import {ConfirmDialogModule, ConfirmationService} from 'primeng/primeng';
import { TooltipComponent } from './components/tooltip/tooltip.component';
import { MapComponent } from './components/map/map.component';
import { PageNotFoundServiceResolver } from './services/pageNotFoundService/PageNotFoundServiceResolver';

const appRoutes: Routes = [
  {path: 'login', component: LoginformComponent},
  {path: '', canActivate: [CanActivateTeamService], redirectTo: 'dashboard', pathMatch: 'full'},
  {path: 'dashboard', canActivate: [CanActivateTeamService], component: DashboardComponent, resolve: {
    user: DashboardResolverService
  },
    children: [
      {
        path:'',
        redirectTo: 'statistics',
        pathMatch: 'full' 
      },
      {path: 'users', canActivate: [CanActivateTeamService], component: UserListComponent, resolve: {
        users: UserDetailsResolverService
      }
      },
      {path: 'users/:id', component: UserPageComponent, resolve: {
        user: UserByIdResolverService
      }},
      {path: 'statistics', canActivate: [CanActivateTeamService], component: StatisticsPageComponent },
      {path: 'websocket', canActivate: [CanActivateTeamService], component: WebsocketComponent},
      {path: 'map', canActivate: [CanActivateTeamService], component: MapComponent}
    ]},
  {path: '**', component: PageNotFoundComponent, resolve: {
    data: PageNotFoundServiceResolver
  }}

];


@NgModule({
  declarations: [
    SearchByNamePipe,
    AppComponent,
    LoginformComponent,
    LogoComponent,
    PageNotFoundComponent,
    DashboardComponent,
    UserListComponent,
    UserPageComponent,
    StatisticsPageComponent,
    WebsocketComponent,
    TooltipComponent,
    MapComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    RouterModule.forRoot(appRoutes),
    TabMenuModule,
    NgbModule.forRoot(),
    BrowserAnimationsModule, 
    ModalModule.forRoot(),
    BootstrapModalModule,
    DropdownModule,
    DataTableModule,
    SharedModule,
    OrderListModule,
    ChartModule,
    MessagesModule,
    ConfirmDialogModule
  ],
  providers: [PermissionService, ConfirmationService, AuthInterceptorService, JwtHelperService, LoadingService, CanActivateTeamService, UserDetailsResolverService, UserByIdResolverService, UserService, UserLoggedDetailsService, DashboardResolverService, PageNotFoundServiceResolver,
    {provide: APP_CONFIG, useValue: APP_DI_CONFIG},
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptorService,
      multi: true,
    }
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ],
  bootstrap: [AppComponent]
})
export class AppModule {}



