import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { Injectable } from '@angular/core';
import { LoginSucceedType } from '../http/resultsTypes/loginsucceedtype';

@Injectable()
export class UserLoggedDetailsService {
userUpdatedSubject: Subject<LoginSucceedType>;
counter: number = 0;
user: LoginSucceedType;
    constructor() {
    this.counter++;
    this.userUpdatedSubject = new Subject<LoginSucceedType>();
    this.user = null;
    }

    userUpdated(user: LoginSucceedType){
        this.user = user;
        this.userUpdatedSubject.next(user);
    }

    getObservable(): Observable<any> {
        return this.userUpdatedSubject.asObservable();
    }
    getUser(): LoginSucceedType {
        return this.user;
    }
}


