import { User } from './../components/user-list/Struct/user';
import { Pipe, PipeTransform, NgModule } from '@angular/core';

@NgModule({
  imports:        [],
  declarations:   [],
  exports:        [],
})

@Pipe({
  name: 'searchByName'
})
export class SearchByNamePipe implements PipeTransform {
  type: Number;
  constructor(){
    this.type = 1;
  }
  transform(allUsers: User[], letters: any, newType: Number){
    var users = allUsers.filter(user => {
        switch(newType){
          case 0:
            return user.id == letters;
          case 1:
            return user.firstName.includes(letters)
          case 2:
            return user.lastName.includes(letters)
          case 3:
            return user.email.includes(letters)
          case 4:
            return user.phoneNumber.includes(letters)
          case 5:
            return user.birthDay.getDate().valueOf() == letters.getDate().valueOf();
        }
      }
    );

    return users;
  }
  changePipeType(type){
    this.type = type;
  }

}


